import CustomModal from "../../../components/CustomModal";
import CustomInputContainer from "../../../components/CustomInputConatiner";
import {useState} from "react";
import {Grid} from "@material-ui/core";
import Button from "../../../components/Button";
import {addNewInvestor} from "../../../_helpers/serverApi";
import TextWrapper from "../../../components/TextWrapper";
import theme from "../../../theme";
import {correctString, ValidateNumber} from "../../../components/CustomInputConatiner/inputValidation";
import {isAddress, isEmailId} from "../../../_helpers/validators";


interface IPorps {
    openModal: boolean;
    onClose: () => void;
    onSuccess: () => void;
}

interface IInputField {
    value: string;
    status: 'default' | 'error' | 'warning';
    msg: string;
}

const initInputField: IInputField = {
    value: '',
    status: 'default',
    msg: '',
}

const AddInvestorModal = (props: IPorps) => {
    const {openModal, onSuccess, onClose} = props

    const [name, setName] = useState<IInputField>(initInputField);
    const [walletAddress, setwalletAddress] = useState<IInputField>(initInputField);
    const [emailId, setemailId] = useState<IInputField>(initInputField);
    const [teleId, setteleId] = useState<IInputField>(initInputField);
    const [investmentAtPrivateSale, setinvestmentAtPrivateSale] = useState<IInputField>(initInputField);
    const [tokenAllocated, settokenAllocated] = useState<IInputField>(initInputField);
    const [allocatedAtPrice, setallocatedAtPrice] = useState<IInputField>(initInputField);

    const [errorMsg, setErrorMsg] = useState<string>('');
    const [addingInvestor, setAddingInvestor] = useState<boolean>(false)

    // var disabled: boolean = name !== '' ||
    //     walletAddress !== '' ||
    //     emailId !== '' ||
    //     teleId !== ''
        // Number(investmentAtPrivateSale) > 0 ||
        // Number(tokenAllocated) > 0 ||
        // Number(allocatedAtPrice) > 0

    const resetFields = () => {
        setName(initInputField)
        setemailId(initInputField)
        setwalletAddress(initInputField)
        setteleId(initInputField)
        settokenAllocated(initInputField)
        setallocatedAtPrice(initInputField)
        setinvestmentAtPrivateSale(initInputField)
        setErrorMsg('')
    }

    const onNumericalValueChange = (value: string) => {
        if (value === '') {
        } else {
            if (!ValidateNumber(value)) return null
        }

        if (Number(value) && Number(value) <= 0) return null;
        return (value.length > 1 ? correctString(value) : value);
    }

    const blankFields = () => {
        let status = true;
        if (name.value === '') {
            setName({
                value: name.value,
                status: 'error',
                msg: 'name is required',
            })
            status = false;
        }
        if (walletAddress.value === '') {
            setwalletAddress({
                value: walletAddress.value,
                status: 'error',
                msg: 'walletAddress is required',
            })
            status = false;
        }
        if (emailId.value === '') {
            setemailId({
                value: emailId.value,
                status: 'error',
                msg: 'emailid is required',
            })
            status = false;
        }
        if (teleId.value === '') {
            setteleId({
                value: teleId.value,
                status: 'error',
                msg: 'telegram id is required',
            })
            status = false;
        }
        if (investmentAtPrivateSale.value === '') {
            setinvestmentAtPrivateSale({
                value: investmentAtPrivateSale.value,
                status: 'error',
                msg: 'Total Investment at private sale is required',
            })
            status = false;
        }
        if (tokenAllocated.value === '') {
            settokenAllocated({
                value: tokenAllocated.value,
                status: 'error',
                msg: 'Number of token allocated cannot is required',
            })
            status = false;
        }
        if (allocatedAtPrice.value === '') {
            setallocatedAtPrice({
                value: allocatedAtPrice.value,
                status: 'error',
                msg: 'Price at Token Allocated cannot is required',
            })
            status = false;
        }
        console.log('blankFields', status)
        return status

    }

    const validateFields = () => {
        let status = true;
        if (!isAddress(walletAddress.value)) {
            setwalletAddress({
                value: walletAddress.value,
                status: 'error',
                msg: 'Invalid Wallet Address',
            })
            status = false;
        }
        if (!isEmailId(emailId.value)) {
            setemailId({
                value: emailId.value,
                status: 'error',
                msg: 'Invalid Email Id',
            })
            status = false;
        }
        if (investmentAtPrivateSale.value === '0') {
            setinvestmentAtPrivateSale({
                value: investmentAtPrivateSale.value,
                status: 'error',
                msg: 'Total Investment at private sale cannot be 0',
            })
            status = false;
        }
        if (tokenAllocated.value === '0') {
            settokenAllocated({
                value: tokenAllocated.value,
                status: 'error',
                msg: 'Number of token allocated cannot be 0',
            })
            status = false;
        }
        if (allocatedAtPrice.value === '0') {
            setallocatedAtPrice({
                value: allocatedAtPrice.value,
                status: 'error',
                msg: 'Price at Token Allocated cannot be 0',
            })
            status = false;
        }
        console.log('validateFields', status)
        return status
    }

    const addInvestor = () => {
        if (blankFields()) {
            console.log('addInvestor blankFields', blankFields())
            if (validateFields() && errorMsg === '') {
                const body = {
                    "name": name.value,
                    "email": emailId.value,
                    "walletAddress": walletAddress.value,
                    "tokenAllocated": Number(tokenAllocated.value) || 0,
                    "allocatedAtPrice": Number(allocatedAtPrice.value) || 0,
                    "telegramUsername": teleId.value,
                    "investmentAtPrivateSale": Number(investmentAtPrivateSale.value) || 0,

                    "currentBalance": 0,
                    "tenMinutePriorBalance": 0,
                    "countryCode": 91,
                    "phoneNumber": 7756885345,
                    "profitLoss": 0,
                    "proftLossIndicator": false,
                    "change": 0,
                }
                setAddingInvestor(true)
                console.log('addInvestor', validateFields(), errorMsg, body)
                /*addNewInvestor(body)
                    .then((data) => {
                        console.log('addNewInvestor', data)
                        if (data?.error) {
                            alert(data?.error)
                        } else {
                            onSuccess();
                        }
                    })
                    .catch((error) => {
                        console.log('addNewInvestor error', error)
                        alert('Internal Server Error, Try Again later')
                    })
                    .finally(() => {
                        resetFields()
                        setAddingInvestor(false)
                    })*/
            }
        }
    }

    const onAllocatedAtPriceChange = (data: string) => {
        if (Number(investmentAtPrivateSale.value) && Number(data)) {
            const temp = Number(investmentAtPrivateSale.value) / Number(data)
            settokenAllocated({
                value: temp.toString(),
                status: 'default',
                msg: '',
            })
        } else {
            settokenAllocated({
                value: '0',
                status: 'default',
                msg: '',
            })
        }
        setallocatedAtPrice({
            value: data,
            status: 'default',
            msg: '',
        });
    }

    const onTokenAllocatedChange = (data: string) => {
        if (Number(investmentAtPrivateSale.value) && Number(data)) {
            const temp = Number(investmentAtPrivateSale.value) / Number(data)
            setallocatedAtPrice({
                value: temp.toString(),
                status: 'default',
                msg: '',
            });
        } else {
            setallocatedAtPrice({
                value: '0',
                status: 'default',
                msg: '',
            });
        }
        settokenAllocated({
            value: data,
            status: 'default',
            msg: '',
        })
    }

    const onTotalInvestmentChange = (data: string) => {
        setallocatedAtPrice({
            value: '',
            status: 'default',
            msg: '',
        });
        settokenAllocated({
            value: '',
            status: 'default',
            msg: '',
        })
        setinvestmentAtPrivateSale({
            value: data,
            status: 'default',
            msg: '',
        })
    }

    return (
        <CustomModal
            closeButton
            handleClose={() => {
                resetFields()
                onClose()
            }}
            open={openModal}
            modalTitleStyle={{}}
            modalContainerStyle={{}}
            modalBodyStyle={{}}
            title={`Add Investor`}
        >
            <section>
                <CustomInputContainer
                    ILabelValue={'Investor Name'}
                    DefaultValue={'Eg: Elon Musk'}
                    setText={(data) => setName({
                        value: data,
                        status: 'default',
                        msg: '',
                    })}
                    value={name.value}
                    Istate={name.status}
                    Imsg={name.msg}

                />
                <CustomInputContainer
                    ILabelValue={'Wallet Address'}
                    DefaultValue={'Eg: 0xcab3e009Ff3d670.....75e81C8E4E2452606E8'}
                    setText={(data) => setwalletAddress({
                        value: data,
                        status: 'default',
                        msg: '',
                    })}
                    value={walletAddress.value}
                    Istate={walletAddress.status}
                    Imsg={walletAddress.msg}
                />
                <CustomInputContainer
                    ILabelValue={'Email Id'}
                    DefaultValue={'Eg: elonmusk@gmail.com'}
                    setText={(data) => setemailId({
                        value: data,
                        status: 'default',
                        msg: '',
                    })}
                    value={emailId.value}
                    Istate={emailId.status}
                    Imsg={emailId.msg}
                />
                <CustomInputContainer
                    ILabelValue={'Telegram Id'}
                    DefaultValue={'Eg:https://telegram.com/@<InvestorId>'}
                    setText={(data) => setteleId({
                        value: data,
                        status: 'default',
                        msg: '',
                    })}
                    value={teleId.value}
                    Istate={teleId.status}
                    Imsg={teleId.msg}
                />
                <CustomInputContainer
                    ILabelValue={'Total Investment at private sale'}
                    DefaultValue={'$ Some Dollars'}
                    setText={(data) => {
                        if (onNumericalValueChange(data) !== null) onTotalInvestmentChange(data)
                    }}
                    value={investmentAtPrivateSale.value.toString()}
                    Istate={investmentAtPrivateSale.status}
                    Imsg={investmentAtPrivateSale.msg}
                />
                <CustomInputContainer
                    ILabelValue={'Number of token allocated'}
                    DefaultValue={'Some MAHA'}
                    setText={(data) => {
                        if (onNumericalValueChange(data) !== null) onTokenAllocatedChange(data)
                    }}
                    value={tokenAllocated.value.toString()}
                    Istate={tokenAllocated.status}
                    Imsg={tokenAllocated.msg}
                />
                <CustomInputContainer
                    ILabelValue={'Price at Token Allocated'}
                    DefaultValue={'$ Some Dollars'}
                    setText={(data) => {
                        if (onNumericalValueChange(data) !== null) onAllocatedAtPriceChange(data)
                    }}
                    value={allocatedAtPrice.value.toString()}
                    Istate={allocatedAtPrice.status}
                    Imsg={allocatedAtPrice.msg}
                />
            </section>
            <section>
                <TextWrapper
                    text={errorMsg}
                    Fcolor={theme.color.red[300]}
                />
            </section>
            <section>
                <Grid container spacing={2} style={{paddingTop: '12px'}}>
                    <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                        <Button
                            variant={'transparent'}
                            onClick={() => {
                                resetFields()
                                onClose()
                            }}
                        >
                            Cancel
                        </Button>
                    </Grid>
                    <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                        <Button
                            onClick={() => addInvestor()}
                            disabled={addingInvestor}
                        >{addingInvestor? 'Adding Investor': 'Add Investor'}</Button>
                    </Grid>
                </Grid>
            </section>
        </CustomModal>
    )
}

export default AddInvestorModal;