export const Host = 'http://172.105.58.101:3000'

export async function callApi(endpoint: string, method: string = 'get', body?: any) {
    const url = `${Host}/api/${endpoint}`;
    const token = (await localStorage.getItem('token')) || '';
    const headers: any = {
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET,POST,OPTIONS",
        "Access-Control-Allow-Headers": "access-control-allow-headers,access-control-allow-methods,access-control-allow-origin,content-type",
        'content-type': 'application/json',
    }
    if (token) {
        headers['Authorization'] = `Bearer ${token}`
        headers['x-jwt'] = token
    }
    console.log('api start callApi', new Date().getMinutes(),'min', new Date().getSeconds(),'s', new Date().getMilliseconds(),'ms', method.toUpperCase(), endpoint, body);
    return await fetch(url, {
        headers,
        method,
        body: JSON.stringify(body)
    })
        .then(async (response) => {
            console.log('api done callApi', new Date().getMinutes(),'min', new Date().getSeconds(),'s', new Date().getMilliseconds(),'ms', response.ok, method.toUpperCase(), endpoint);
            // console.log(response);
            if (!response.ok) {
                return response.json()
                    .then((json) => {
                        // if (json.message === 'Bad JWT') AsyncStorage.removeItem('@BearerToken')
                        return Promise.reject(json)
                    });
            }
            return response.json();
        });
}

