import React from "react";
import styled from "styled-components";

import '../../../customCss/Custom-Modals.css'
import logoutIcon from '../../../assets/icons/Logout.svg'
import TextWrapper from "../../TextWrapper";
import theme from "../../../theme";
import {history} from "../../../_helpers/history";

const AccountOptionsModal = () => {
    const logout = () => {
        localStorage.removeItem('token');
        localStorage.setItem("userLoggedIn", "false");
        history.push("/auth/login");
        history.go(0);
    }

    return (
        <div className="custom-modal-main">
            <ModalStyle className="custom-modal custom-mahadao-material1">
                <div className="custom-modal-container">
                    <Options className="single-line-center-start" onClick={() => logout()}>
                        <img src={logoutIcon} alt={'logout'} height={24}/>
                        <TextWrapper
                            text={'Logout'}
                            fontWeight={600}
                            Fcolor={theme.color.transparent[100]}
                            Fmargin={'0 0 0 10px'}
                        />
                    </Options>
                </div>
            </ModalStyle>
        </div>
    )
}

export default AccountOptionsModal;

const ModalStyle = styled.div`
  min-width: 204px;
  max-width: 204px;
  height: max-content;
`

const Options = styled.div`
  width: 100%;
  height: auto;
  cursor: pointer;
`