import React, {useEffect, useState} from "react";
import { DateRangePicker } from 'react-date-range';

import 'react-date-range/dist/styles.css'; // main css file
import 'react-date-range/dist/theme/default.css'; // theme css file
import '../../customCss/CustomDateRangePicker.css'
import Button from "../Button";
import styled from "styled-components";

interface Iprops {
    selectedDates: {
        startDate: any,
        endDate: any
    };
    onApplyPressCallback: (data: {
        startDate: any,
        endDate: any
    }) => void,
    onClearPressCallback: (data: {
        startDate: any,
        endDate: any
    }) => void,

}

const CustomDateRangePicker = (props: Iprops) => {
    const {selectedDates, onApplyPressCallback, onClearPressCallback} = props;

    const [value, setValue] = useState<any>([{
        startDate: null,
        endDate: new Date(),
        key: 'selection'
    }]);
    const [showDateModal, setShowDateModal] = useState<boolean>(false);

    useEffect(() => {
        setValue([{
            startDate: selectedDates.startDate,
            endDate: selectedDates.endDate,
            key: 'selection'
        }])
    }, [selectedDates.startDate, selectedDates.endDate])

    const onApplyPress = () => {
        onApplyPressCallback({
            startDate: value[0].startDate,
            endDate: value[0].endDate,
        })
        setShowDateModal(false);
    }

    const onClearPress = () => {
        onClearPressCallback({
            startDate: null,
            endDate: new Date(),
        })
        setShowDateModal(false);
    }

    return (
        <div className="">
            <Button onClick={() => setShowDateModal(!showDateModal)} variant="outlined">
                { value[0].startDate !== null
                    ? `${new Date(value[0].startDate).toDateString()} - ${new Date(value[0].endDate).toDateString()}`
                    : `Pick Date`
                }
            </Button>
            {showDateModal &&
                (<div style={{position: 'relative'}}>
                        <ButtonContainer>
                            <div className="marginB08">
                                <Button
                                    onClick={() => onApplyPress()}
                                    // disabled={value[0].startDate !== null && value[0].endDate !== null }
                                >
                                    Apply
                                </Button>
                            </div>
                            <div className="marginB08">
                                <Button
                                    onClick={() => onClearPress()}
                                    variant={'transparent'}
                                >
                                    Clear
                                </Button>
                            </div>
                            <Button onClick={() => setShowDateModal(false)} variant={'outlined'}>
                                Cancel
                            </Button>
                        </ButtonContainer>
                    <DateRangePicker
                        onChange={(data: any) => setValue([data.selection])}
                        showSelectionPreview={true}
                        moveRangeOnFirstSelection={false}
                        months={1}
                        ranges={value}
                        direction="vertical"
                        scroll={{enabled: true}}
                        maxDate={new Date()}
                        color={'#FF7F57'}
                    />
                </div>
            )}
        </div>
    )
}

export default CustomDateRangePicker;

const ButtonContainer = styled.div`
  height: 50px;
  min-width: 150px;
  max-width: 150px;
  position: absolute;
  z-index: 101;
  right: 0;
  top: 240px;
  padding: 10px 20px;
`