import React, {useEffect, useRef} from "react";
import Neovis from "neovis.js";

interface Iprops {
    walletAddress: string;
}

const NEO4J_URI = "neo4j://172.105.58.101:7687";
const NEO4J_USER = "neo4j";
const NEO4J_PASSWORD = "root";

const Neo4jChart = (props: Iprops) => {
    const {walletAddress} = props;

    const visRef = useRef();

    useEffect(() => {
        const config = {
            container_id: 'id_chart',
            server_url: NEO4J_URI,
            server_user: NEO4J_USER,
            server_password: NEO4J_PASSWORD,
            labels: {
                Troll: {
                    caption: "user_key",
                    size: "pagerank",
                    community: "community",
                },
            },
            relationships: {
                RETWEETS: {
                    caption: false,
                    thickness: "count",
                },
            },
            arrows: true,
            initial_cypher:
                `MATCH (a:Person)-[r]-(b) RETURN a,r,b`,
        };
        const vis = new Neovis(config);
        vis.render();
    }, [NEO4J_URI, NEO4J_USER, NEO4J_PASSWORD, walletAddress]);

    return (
        <div
            id={'id_chart'}
            style={{
                width: `100%`,
                height: `600px`,
                backgroundColor: "transparent",
                marginBottom: '200px'
            }}
            className={'custom-mahadao-material2'}
        />
    );
};

export default Neo4jChart;