import React, {useEffect, useState} from "react";

import CustomModal from "../../CustomModal";
import TextWrapper from "../../TextWrapper";
import Loader from 'react-spinners/BeatLoader';
import {importInvestorsData} from "../../../_helpers/serverApi";

interface IPorps {
    openModal: boolean;
    onClose: () => void;
    onSuccess: () => void;
    data: any;
}

const UploadingModal = (props: IPorps) => {
    const {openModal, onSuccess, onClose, data} = props

    const [UploadingData, setUploadingData] = useState<boolean>(false)

    useEffect(() => {
        if (openModal) {
            importInvestorsData(data)
                .then((data) => {
                    console.log('importInvestorsData', data);
                    if (data?.success) {
                        onSuccess()
                    }
                    if (data?.error) {
                        alert('Something went wrong');
                    }
                })
                .catch((error) => {
                    console.log('error importInvestorsData', error);
                    alert('Internal Server Error');
                })
                .finally(() => {
                })
        }
    }, [openModal])

    return (
        <CustomModal
            handleClose={() => {}}
            open={openModal}
            modalTitleStyle={{}}
            modalContainerStyle={{}}
            modalBodyStyle={{}}
            title={`Import Investors by CSV`}
        >
            <div className="text-center">
                <Loader color={'#ffffff'} loading={true} size={12} margin={2} />
                <TextWrapper
                    text={`Please wait few minutes while file is uploading.`}
                    Fmargin={'12px 0 0 0'}
                />
            </div>
        </CustomModal>
    )
}

export default UploadingModal;