import downloadIcon from "../../assets/icons/Download.svg";
import React, {useState} from "react";
import UploadingModal from "./modal/UploadingModal";
import CustomSuccessModal from "../CustomSuccessModal";

interface Iprops {
    type: 'investors'
}

const ImportCSV = (props: Iprops) => {
    const {type} = props;

    const [selectedFile, setSelectedFile] = useState<any>(null);
    const [uploadFileModal, setUploadFileModal] = useState<boolean>(false)
    const [successModal, setSuccessModal] = useState<boolean>(false);

    const hiddenFileInput = React.useRef(null);

    const handleClick = () => {
        //@ts-ignore
        hiddenFileInput?.current?.click();
    };

    const handleChange = async (event: any) => {
        const fileUploaded = event.target.files[0];

        const formData = await new FormData();
        await formData.append(
            "file",
            fileUploaded,
        );

        console.log('formData', formData, fileUploaded)
        setSelectedFile(formData);
        setTimeout(() => {
            setUploadFileModal(true)
        }, 1000)
    };

    const sendCsv = () => {
        const formData = new FormData();
        formData.append(
            "file",
            selectedFile,
            selectedFile.name
        );

    }

    return (
        <>
            <div onClick={() => handleClick()} className="single-line-center-start pointer">
                Import Excel
                <img src={downloadIcon} alt={'download'} height={18} className="marginL04 "/>
                <input
                    type="file"
                    ref={hiddenFileInput}
                    onChange={handleChange}
                    style={{display: 'none'}}
                    accept='.xls, .xlsx'
                />
            </div>
                <UploadingModal
                    openModal={uploadFileModal}
                    onClose={() => setUploadFileModal(false)}
                    onSuccess={() => {
                        setUploadFileModal(false)
                        setSuccessModal(true)
                    }}
                    data={selectedFile}
                />
                <CustomSuccessModal
                    modalOpen={successModal}
                    setModalOpen={() => {
                        window.location.reload();
                        setSuccessModal(false)
                    }}
                    title={'Your file is uploaded you can now track your investors investments'}
                />
            </>
    )
}

export default ImportCSV;